const initialState = {
  items: [],
  showCartSidebar: false,
  totalPrice: 0,
  totalCount: 0,
};

const cart = (state = initialState, action) => {
  switch (action.type) {
    case 'ADD_PRODUCT_CART':
      return {
        ...state,
        items: [...state.items, action.payload],
        showCartSidebar: true,
      };
    case 'SHOW_CART_SIDEBAR':
      return {
        ...state,
        showCartSidebar: true,
      };
    case 'CLOSE_CART_SIDEBAR':
      return {
        ...state,
        showCartSidebar: false,
      };
    default:
      return state;
  }
};

export default cart;
